@font-face {
  font-family: 'Gentium-Bold';
  src: url(../assets/fonts/GentiumBookBasic-Bold.ttf);
}

@font-face {
  font-family: 'Gentium-BoldItalic';
  src: url(../assets/fonts/GentiumBookBasic-BoldItalic.ttf);
}

@font-face {
  font-family: 'Gentium-Italic';
  src: url(../assets/fonts/GentiumBookBasic-Italic.ttf);
}

@font-face {
  font-family: 'Gentium-Regular';
  src: url(../assets/fonts/GentiumBookBasic-Regular.ttf);
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
}

body {
  padding: 0;
  margin: 0;
  border: 0;
  font-family: 'Gentium-Regular';
  font-size: 22px;
  line-height: 26px;
  background-color: #000000;
  color: #ffffff;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-family: 'Gentium-Bold', serif;
}

/* ================================================================== */

.title-page {
  background: #000000;
  opacity: 1;
  transition: opacity 1s;
}

