.title-wrapper {
  width: auto;
  height: 100vh;
  margin: 0 auto;
  background: url(../assets/png/j-retrato-de-cardenal-rafael-360.png) center center / auto 100% no-repeat;
}

.settings-btn {
  position: absolute;
  top: 20px;
  right: 25px;
  width: 26px;
  height: 36px;
  background: url(../assets/svg/carbon_settings.svg) center center/contain no-repeat;
  cursor: pointer;
  user-select: none;
}

.main {
  position: absolute; 
  left: 50%;             
  top: 50%;     
  transform: translate(-50%, -15%);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 260px;
}

.title {
  height: 82px;
  width: 220px;
  margin: 0 auto;
  user-select: none;
}

.img-title {
  height: 100%;
  width: 100%;
}

.buttons {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  row-gap: 20px;
}

.btn {
  height: 56px;
  width: 268px;
  padding: 13px;
  border: 2px solid #ffffff;
  border-radius: 31px;
  text-align: center;
  cursor: pointer;
  user-select: none;
}

.btn:hover,
.btn:hover {
  color: #000;
	background: #ffffff;
}

.btn:active,
.btn:active {
  color: #000;
	background: #FFBCA2;
}

.artist-picture-active{
	color: #000;
	background: #FFBCA2;
	border: 1px solid #FFBCA2;
	transition: 0.5s;
}

.footer {
  position: fixed;
  bottom: 0;
  padding: 20px;
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  /* opacity: 1; */
}

.rsshool {
  width: 85px;
  height: 31px;
  background: url(../assets/svg/rs_school_js.svg) center center/contain no-repeat;
}

.course-link {
  display: block;
  width: 100%;
  height: 100%;
}

.github-link {
  color: inherit;
  text-decoration: none;
}

@media (min-width: 520px) {

  .main {
    transform: translate(-50%, -50%);
    height: 320px;
  }

  .title-wrapper {
    background: url(../assets/png/j-retrato-de-cardenal-rafael-520.png) center center / auto 100% no-repeat;
  }

  .title {
    height: 120px;
    width: 321px;
  }

  .buttons {
    margin: 0 auto;
  }
}


@media (min-width: 768px) {

  .title-wrapper {
    background: url(../assets/png/j-retrato-de-cardenal-rafael.png) center center / auto 100% no-repeat;
  }

  .main {
    transform: translate(-50%, -30%);
    height: 320px;
    width: 614px;
  }

  .title {
    height: 164px;
    width: 440px;
  }

  .buttons {
    flex-direction: row;
    justify-content: center;
    column-gap: 40px;
  }

  .artistBtn, 
  .pictureBtn {
    height: 62px;
    width: 287px;
  }

}